import React from 'react';
import { Nav, Navbar } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUser } from '@fortawesome/free-solid-svg-icons';
import styles from '../styles/headerAdmin.module.css';
import logo from '../images/logo.png';

const HeaderAdmin = () => {

    return (
        <Navbar className={`${styles.header}`} expand='md' collapseOnSelect>
            <Navbar.Brand as={Link} to='/admin' className={`${styles.navbar_brand}`}>
                <img 
                    className={`${styles.img_logo} img-fluid`} 
                    src={logo}
                    alt='Logo'
                />
            </Navbar.Brand>

            <Navbar.Toggle aria-controls="navbar-nav" className={`${styles.nav_toggle}`} />

            <Navbar.Collapse id="navbar-nav">
                <Nav className="mr-auto">
                    <Nav.Link as={Link} to="/admin" className={`${styles.nav_link}`}>Incio</Nav.Link>
                    <Nav.Link as={Link} to="/admin/questoes" className={`${styles.nav_link}`}>Questes</Nav.Link>
                    <Nav.Link as={Link} to="/admin/planos" className={`${styles.nav_link}`}>Planos</Nav.Link>
                    <Nav.Link as={Link} to="/admin/compras" className={`${styles.nav_link}`}>Compras</Nav.Link>
                    <Nav.Link as={Link} to="/admin/usuarios" className={`${styles.nav_link}`}>Usurios</Nav.Link>
                </Nav>
                <Nav className="ml-auto">
                    <Nav.Link as={Link} to="/admin/perfil" className={`${styles.nav_link}`}>
                        <FontAwesomeIcon icon={faUser} className={`${styles.nav_icon}`} />
                    </Nav.Link>
                </Nav>
            </Navbar.Collapse>
        </Navbar>
    )
}

export default HeaderAdmin;